import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  // Lazy async modules
  {
    path: 'login', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\+login\\login.module')['LoginModule']);  });})
  },
  {
    path: 'register', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\+register\\register.module')['RegisterModule']);  });})
  },
  {
    path: 'profile', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\+profile\\profile.module')['ProfileModule']);  });})
  },
  {
    path: 'admin', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\+admin\\admin.module')['AdminModule']);  });})
  },
  {
    path: 'examples', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\+examples\\examples.module')['ExamplesModule']);  });})
  }
];

export const routing = RouterModule.forRoot(routes);



